.modal {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  display: none;
  flex-direction: column;
  background-color: $color-white;
  margin-left: auto;
  margin-right: auto;
  overflow: scroll;
  z-index: 100;

  &--add {
    width: 500px;
    top: 35%;
    padding-top: 47px;
    padding-bottom: 54px;

    @include mobile {
      width: 290px;
      top: 16%;
      padding-top: 38px;
      padding-bottom: 45px;
    }
  }

  &--login {
    position: fixed;
    display: none;
    width: 100%;
    height: 100%;
  }

  &--active {
    display: flex;
  }
}

.modal__add-to-cart {
  position: relative;
  width: 86%;
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;

  @include tablet {
    width: 88.5%;
  }

  @include mobile {
    width: 89.5%;
  }
}

.modal__add-to-cart h2 {
  @include style-reset;
  font-family: $font-family-secondary;
  font-weight: bold;
  font-size: 26px;
  line-height: 34px;
  text-align: center;
  margin-bottom: 42px;

  @include mobile {
    font-size: 24px;
    line-height: 29px;
    margin-bottom: 28px;
  }
}

.modal__container {
  width: 100%;
  display: flex;
  justify-content: space-between;

  &--product {
    margin-bottom: 52px;

    @include mobile {
      margin-bottom: 40px;
    }
  }

  &--subtotal {
    margin-bottom: 36px;

    @include mobile {
      flex-direction: column;
      justify-content: flex-start;
      margin-bottom: 31px;
    }
  }

  &--btn {
    @include mobile {
      flex-direction: column;
      justify-content: flex-start;
    }
  }
}

.modal__container--subtotal p {
  @include style-reset;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;

  @include mobile {
    font-size: 12px;
    line-height: 16px;
    margin-bottom: 12px;
  }
}

.modal__container--subtotal span {
  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.01em;

  @include mobile {
    font-size: 12px;
    line-height: 16px;
  }
}

.modal__img {
  width: 26%;
  margin-right: 20px;
  margin-top: 4px;

  @include tablet {
    width: 26.5%;
  }

  @include mobile {
    width: 39.5%;
    margin-top: 5px;
    margin-right: 15px;
  }
}

.modal__img img {
  @include tablet {
    width: 100%;
  }
}

.modal__counter {
  position: relative;
  min-width: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  padding-right: 2px;

  @include tablet {
    padding-right: 4px;
  }
}

.modal__counter span {
  font-size: 18px;
  line-height: 23px;

  @include mobile {
    font-size: 16px;
    line-height: 21px;
  }
}

.modal__product {
  width: 50%;
  display: flex;
  flex-direction: column;

  @include tablet {
    width: 65%;
  }
}

.modal__product h3 {
  @include style-reset;
  font-family: $font-family-secondary;
  font-weight: 700;
  font-size: 18px;
  line-height: 23px;
  margin-bottom: 8px;

  @include mobile {
    font-size: 16px;
    line-height: 21px;
  }
}

.modal__product p {
  @include style-reset;
  font-size: 18px;
  line-height: 23px;

  @include mobile {
    font-size: 16px;
    line-height: 21px;
  }
}
